export class Queue<T> {
  items: T[] = [];
  //
  enqueue(element: T) {
    this.items.push(element);
  }
  //
  dequeue(): T | undefined {
    const result = this.items.shift();
    return result;
  }
  //
  front(): T | undefined {
    return this.items[this.items.length - 1];
  }
  //
  isEmpty(): boolean {
    return !this.items.length;
  }
  //
  size(): number {
    return this.items.length;
  }
  //
  clear() {
    this.items = [];
  }
  // 
  print() {
    console.log(this.items.toString());
  }
}
