    if (!(mapArr[rowCount - 1][i] == "2")) {
      mapArr[rowCount - 1][i] = "100";
      exit = [rowCount - 1, i];
      break;
    }
  }

  return {
    mapArr,
    roadArr,
    entrance,
    exit,
  };
};

