  let data: {
    hasRoad: boolean;
    distance: number;
    roadArr: boolean[][];
  } = {
    hasRoad: false,
    distance: 0,
    roadArr: [],
  };
  if (statisticalData.type === "bfs") {
    data = bfs(
      state.mapArr,
      state.entrance,
      state.exit,
      state.rowCount,
      state.colCount
    );
  } else if (statisticalData.type === "dfs") {
    data = dfs(
      state.mapArr,
      state.entrance,
      state.exit,
      state.rowCount,
      state.colCount
    );
  }

  state.roadArr = data.roadArr;

  let end_time = new Date().getTime();
  statisticalData.spendTime = end_time - start_time;
  statisticalData.distance = data.distance;

  if (data.hasRoad) {
    window.$message.success(`! :${statisticalData.spendTime} ms`);
  } else {
    window.$message.error("~");
  }
  setTimeout(() => {
    window.$loading.finish();
  }, 0);
};

