  statisticalData.spendTime = end_time - start_time;
  statisticalData.distance = distance;
  state.roadArr = roadArray;

  if (hasRoad) {
    window.$message.success(
      "! :" + statisticalData.spendTime + "ms"
    );
  } else {
    window.$message.error("~");
  }
});

